/*background colours*/

@basic-grey: #F2F2F2;
@basic-green: #68B738;
@basic-green-promo: rgba(104, 183, 56, 0.85);
@basic-darker-green: #5EAA2F;
@basic-grey: #F2F2F2;
@basic-grey-example: #EAEAEA;
@basic-grey-button-hover: #EBEBEB;

/*font colours*/
@basic-white: #FFFFFF;
@basic-black: #000000;
@basic-text: #444444;
@basic-text-darker: #111111;

/*form colours*/
@form-field-border: #E7E7E7;
@form-error-field-border: #FF8282;
@radio-specials-hover: #BABABA;


/*other styleguide parameters*/
@divider: #D9D9D9;
@divider-2: #CDCDCD;
@divider-3: #E6E6E6;
@footer-socials: #666666;
@show-more-plus: #D3D3D3;

@logo-hover_opacity: 0.8;
@logo-pressed-opacity: 0.6;
@link-text-pressed-opacity: 0.3;

@main-nav-active_line-width: 2px;

@basic-font: "Arial";
@special-font: "Oswald";

/*Mixins*/

.list-normalize {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.link-normalize {
  text-decoration: none;
  color: @basic-black;
}

.set-special-font {
  font-family: @special-font, @basic-font, sans-serif;
}
